.products {
  @include list-style-reset();
}

.products__item {
  padding-bottom: 23px;
}

@media (min-width: $tablet-width) {
  .products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .products__item {
    position: relative;
  }
}

@media (min-width: $desktop-width) {
  .products {
    justify-content: normal;
  }
}
